export default class ArtyomCommandsManager {

    // The ArtyomCommandsManager class expects as argument in the constructor
    // an already declared instance of Artyom.js
    constructor (ArtyomInstance){
        this._artyom = ArtyomInstance;
    }
    
    // Execute the loadCommands method to inject the methods to the instance of Artyom
    loadCommands(){
        let Artyom = this._artyom;

        // Here you can load all the commands that you want to Artyom
        return Artyom.addCommands([
            {
                indexes: ["Hola", "buenos días", "Buenas tardes"],
                action: () => {
                    //Artyom.say("Hola, como estas?, cuentame como podemos ayudarte");
                    Artyom.say("Hola, bienvenido al confesionario de la alcaldía de barrancabermeja");
                    Artyom.say("Bime como te sientes: ¿triste o feliz?");
                    //alert("Dijiste Hola");
                }
            },
            {
                indexes: ["feliz", "contento" , "Alegre"],
                action: () => {
                    Artyom.say("Me encanta saber que estás bien");
                    Artyom.say("¿te gustaría hablar de empleo o de salud?");
                    //alert("Dijiste Hola");
                }
            },
            {
                indexes: ["triste"],
                action: () => {
                    Artyom.say("Ánimo, en barrancabermeja todos estamos para apoyarte");
                    Artyom.say("¿te gustaría hablar de empleo o de salud?");
                    //alert("Dijiste Hola");
                }
            },
            {
                indexes: ["salud"],
                action: () => {
                    Artyom.say("¿En tu familia ha habido alguien con fiebre o tos en los últimos días?");
                    
                    //alert("Dijiste Hola");
                }
            },
            {
                indexes: ["empleo"],
                action: () => {
                    Artyom.say("¿conoces a alguien que haya perdido su empleo en esta época?");
                    
                    //alert("Dijiste Hola");
                }
            },
            {
                indexes: ["adios", "hasta luego", "chao"],
                action: () => {
                    Artyom.say("Hasta luego, ¡regresa pronto!");
                    //alert("Dijiste Hola");
                }
            },
            /*{
                indexes: ["visitar pagina de kaleido" , "pagina de kaleido"],
                action: () => {
                    Artyom.say("ahí vamos");
                    window.open("https://kaleidolab.co/");
                }
            },*/
            /*{
                indexes: [/(Kaleido|Kalei|quien|hablame) /ig, new RegExp("hablame de Kaleido", "ig")],
                smart:true,
                action: function(i){
                    Artyom.say("Todo bien");
                }
            },*/
            /*{
                indexes: ["Generar reporte de * de este año"],
                smart: true,
                action: (i, month) => {
                    let year = new Date().getFullYear();
                    
                    //Artyom.say(`Generando reporte de ${month} ${year} `);
                    console.log("Mencionaste " + month);
                    Artyom.say("Listo");
                }
            },*/
            /*{
                indexes: ["Háblame * Kaleido", "Quiénes * Kaleido", "Quién * Kaleido"],
                smart: true,
                action: (i, month) => {
                    
                    //Artyom.say(`Generando reporte de ${month} ${year} `);
                    console.log("Mencionaste " + month);
                    Artyom.say("Somos el primer laboratorio de tecnologías de realidad virtual y realidad aumentada en Colombia. Innovamos con su empresa o institución en la generación de experiencias inmersivas de alto impacto.");
                }
            },*/

        ]);
    }
}